    object-fit: cover;
    width: 100vw;
    height: 100vh;
    position: absolute;

  }

  .steps_title_wrapper {
    display: flex;
    justify-content: center;

    font-style: normal;
    height: 200px;
    align-items: center;
    margin-bottom: 2%;

    & h1 {
      font-weight: 800;
      font-size: 72px;
color:rgb(34 34 34 / 33%);
      // color: rgb(34 34 34 / 70%);
      padding: 10px;
    }
    & h2 {
      font-weight: 700;
      font-size: 36px;

      padding: 10px;
    }

    .steps_title_divider {
      position: relative;
      width: 32%;
      height: 0px;
      // left: 357px;
      // top: 49px;
      border: 1px solid #000000;
    }
  }

.steps_wrapper{

  height: max-content;
  // background:url(https://burst.shopifycdn.com/photos/black-wood-angle-texture.jpg?width=925&format=pjpg&exif=1&iptc=1);
