    & h1{
        justify-self: center;
        color: #fff;
        padding: 21px;
        border-radius: 15px 30px 45px;
        background-color: rgba(24, 25, 26, 0.37);
   
        // font-weight:800;
        // font-size: 35px;
        // text-transform: uppercase;
        // // font-size: 2em;
        // letter-spacing: 4px;
        // overflow: hidden;
