    }
    &__mob{
@extend .steps_subtitles;

& h1{
  font-size: 30px !important;
}
    }
}
  // }

  .steps_disp_choices{
    color: #5c5656;
    display: flex;
    // justify-content: center;
    // transform: translate(0, -110px);
    // align-items: flex-start;
    // justify-content: space-around;
    height: -webkit-fill-available;
    justify-content: center;

    &__mob{
      @extend .steps_disp_choices;
      flex-wrap: wrap;
      position: relative;
      transform: none;
    }
  .step_wrapper{
    transition: background-color 5s ease-in-out;
    padding: 35px;
    box-sizing: border-box; 
     min-height: 100%;
     text-align: center;
     filter: grayscale(1);
    //  flex: 1 1 20%;
    cursor: pointer;

     &._active{
      background: -moz-linear-gradient(-45deg, #D9B695 15%,#ececec  100%);
      background: -webkit-linear-gradient(-45deg, #e2a9e5 15%,#2b94e5 100%);
      background: linear-gradient(-45deg, #ececec 15%, #D9B695 100%);
      transition: background-color 3s ease-in-out;
      filter: drop-shadow(2px 4px 6px black);
     }

     &:last-of-type{
      & img , p{
        transform: translateY(31px);
      }
      

     }
     
& p{
    font-size: 20px;
    font-weight: 600;

}

  }
}

